
export default (errors) => {
  if (!(errors && errors.length)) return
  for (let i = 0 ; i < errors.length; i++) {
    let e = errors[i]
    let out
    let schema = e.schema
    switch (e.keyword) {
      case '$ref': {
        out = '' + (e.params.ref)
        break
      }
      case 'additionalItems': {
        out = ''
        let n = e.params.limit
        out += '' + (n) + ''
        break
      }
      case 'additionalProperties': {
        out = ''
        break
      }
      case 'anyOf': {
        out = ' anyOf '
        break
      }
      case 'constant': {
        out = schema.constantName ? (' ' + schema.constantName + ' ') : ''
        break
      }
      case 'custom': {
        out = ' "' + (e.keyword) + ' "'
        break
      }
      case 'dependencies': {
        out = ''
        let n = e.params.depsCount
        out += '' + (e.params.property) + '' + (e.params.deps)
        break
      }
      case 'enum': {
        out = ''
        break
      }
      case 'format': {
        out = '' + (e.params.format)
        break
      }
      case 'formatExclusiveMaximum': {
        out = 'formatExclusiveMaximum '
        break
      }
      case 'formatExclusiveMinimum': {
        out = 'formatExclusiveMinimum '
        break
      }
      case 'formatMaximum': {
        out = ''
        let cond = e.params.comparison + ' ' + e.params.limit
        out += ' ' + (cond)
        break
      }
      case 'formatMinimum': {
        out = ''
        let cond = e.params.comparison + ' ' + e.params.limit
        out += ' ' + (cond)
        break
      }
      case 'maxItems': {
        out = ''
        let n = e.params.limit
        out += ' ' + (n) + ' '
        break
      }
      case 'maxLength': {
        out = ''
        let n = e.params.limit
        out += ' ' + (n) + ' '
        break
      }
      case 'maxProperties': {
        out = ''
        let n = e.params.limit
        out += ' ' + (n) + ' '
        break
      }
      case 'maximum': {
        out = ''
        let cond = e.params.comparison + ' ' + e.params.limit
        out += ' ' + (cond)
        break
      }
      case 'minItems': {
        out = ''
        let n = e.params.limit
        out += ' ' + (n) + ' '
        break
      }
      case 'minLength': {
        out = ''
        let n = e.params.limit
        out += ' ' + (n) + ' '
        break
      }
      case 'minProperties': {
        out = ''
        let n = e.params.limit
        out += ' ' + (n) + ' '
        break
      }
      case 'minimum': {
        out = ''
        let cond = e.params.comparison + ' ' + e.params.limit
        out += ' ' + (cond)
        break
      }
      case 'multipleOf': {
        out = ' ' + (e.params.multipleOf) + ' '
        break
      }
      case 'not': {
        out = ' "not" schema'
        break
      }
      case 'oneOf': {
        out = ' oneOf  schema'
        break
      }
      case 'pattern': {
        out = ' "' + (e.params.pattern) + '"'
        break
      }
      case 'patternGroups': {
        out = ''
        let n = e.params.limit
        out += ' ' + (n) + '  ' + (e.params.reason) + '  ' + (e.params.pattern)
        break
      }
      case 'patternRequired': {
        out = ' ' + (e.params.missingPattern)
        break
      }
      case 'required': {
        const pname = e.params.missingProperty
        out = ((e.schema[pname] && e.schema[pname].title) || pname) + ''
        break
      }
      case 'switch': {
        out = ' ' + (e.params.caseIndex) + '  "switch" , '
        break
      }
      case 'type': {
        out = ' ' + (e.params.type) + ' '
        break
      }
      case 'uniqueItems': {
        out = ' ( ' + (e.params.j) + '  ' + (e.params.i) + ' )'
        break
      }
      default:
        continue
    }
    e.message = out
  }
}



